.imageCarousel .image {
  cursor: pointer;
  width: 360px;
  height: 360px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-transition: background 0.5s;
  -moz-transition: background 0.5s;
  -ms-transition: background 0.5s;
  -o-transition: background 0.5s;
  transition: background 0.5s; }

.imageCarousel .nav {
  font-size: 12px; }
  .imageCarousel .nav .prev, .imageCarousel .nav .next {
    cursor: pointer;
    color: #E63F57;
    padding: 4px; }
    .imageCarousel .nav .prev:hover, .imageCarousel .nav .next:hover {
      color: #d61c37; }
  .imageCarousel .nav .prev,
  .imageCarousel .nav .next,
  .imageCarousel .nav .label {
    display: inline-block; }
  .imageCarousel .nav .label {
    margin: 0 16px; }
    .imageCarousel .nav .label span {
      margin: 0 4px;
      color: #A3A3A3; }
      .imageCarousel .nav .label span.currentIndex {
        color: black; }



