#navBar {
  z-index: 10;
  padding: 16px;
  text-align: center;
  background-color: white;
  -webkit-box-shadow: 1px 0px 4px 1px rgba(0, 0, 0, 0.08);
  box-shadow: 1px 0px 4px 1px rgba(0, 0, 0, 0.08); }
  #navBar:hover .logo {
    height: 120px;
    width: 120px; }
  #navBar:hover .linksFull {
    visibility: visible;
    opacity: 1; }
  #navBar:hover .linksShort {
    visibility: hidden;
    opacity: 0; }
  #navBar .logo {
    background: url("/images/logo.png") no-repeat center center;
    background-size: contain;
    height: 64px;
    width: 64px;
    -webkit-transition: width 0.5s, height 0.5s;
    -moz-transition: width 0.5s, height 0.5s;
    -ms-transition: width 0.5s, height 0.5s;
    -o-transition: width 0.5s, height 0.5s;
    transition: width 0.5s, height 0.5s;
    display: inline-block;
    vertical-align: bottom; }
  #navBar .linksFull,
  #navBar .linksShort {
    -webkit-transition: visibility 0.5s, opacity 0.5s;
    -moz-transition: visibility 0.5s, opacity 0.5s;
    -ms-transition: visibility 0.5s, opacity 0.5s;
    -o-transition: visibility 0.5s, opacity 0.5s;
    transition: visibility 0.5s, opacity 0.5s;
    position: absolute;
    bottom: 32px;
    left: 32px;
    text-align: left;
    font-size: 12px; }
  #navBar .linksFull {
    visibility: hidden;
    opacity: 0; }
  #navBar .linksShort {
    visibility: visible;
    opacity: 1; }
  #navBar a {
    color: #E63F57;
    font-weight: 500;
    text-transform: uppercase;
    display: block;
    margin: 8px 0; }
    #navBar a:hover {
      color: #d61c37; }

@media (max-width: 480px) {
  #navBar {
    text-align: left;
    -webkit-box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.08);
    box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.08); }
    #navBar .logo,
    #navBar:hover .logo {
      height: 48px;
      width: 48px;
      vertical-align: bottom;
      margin: 0 16px 0 0; }
    #navBar .linksFull {
      vertical-align: bottom;
      visibility: visible;
      opacity: 1;
      display: inline-block;
      position: initial;
      border-bottom: 2px solid #BCBCBC; }
      #navBar .linksFull a {
        display: inline-block; }
        #navBar .linksFull a:after {
          content: "/";
          padding: 0 6px;
          color: #A3A3A3; }
    #navBar .linksShort {
      display: none; } }



