.githubContributionsGraph .graphContainer,
.githubContributionsGraph .graphInfo {
  display: inline-block;
  vertical-align: top; }

.githubContributionsGraph .graphInfo {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 32px;
  width: calc(100% - 200px); }
  .githubContributionsGraph .graphInfo .dateInfo,
  .githubContributionsGraph .graphInfo .commits {
    margin: 32px 0; }
  .githubContributionsGraph .graphInfo .dateInfo .date,
  .githubContributionsGraph .graphInfo .commits .num {
    border: 2px solid #E63F57;
    padding: 8px;
    color: #E63F57; }

.githubContributionsGraph .graphContainer {
  position: relative;
  height: 640px;
  max-height: 100vh;
  overflow: hidden; }
  .githubContributionsGraph .graphContainer .graph {
    position: relative;
    height: 100%;
    overflow: auto; }
    .githubContributionsGraph .graphContainer .graph:after {
      content: "";
      height: 124px;
      width: 100%;
      position: absolute; }
  .githubContributionsGraph .graphContainer:after {
    content: "";
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(248, 248, 248, 0)), color-stop(rgba(248, 248, 248, 0.8)), to(#f8f8f8));
    background: -webkit-linear-gradient(top, rgba(248, 248, 248, 0), rgba(248, 248, 248, 0.8), #f8f8f8);
    background: -o-linear-gradient(top, rgba(248, 248, 248, 0), rgba(248, 248, 248, 0.8), #f8f8f8);
    background: linear-gradient(to bottom, rgba(248, 248, 248, 0), rgba(248, 248, 248, 0.8), #f8f8f8);
    z-index: 5;
    height: 120px;
    width: 100%;
    position: absolute;
    bottom: 0; }

.githubContributionsGraph .week {
  display: block; }

.githubContributionsGraph .day {
  display: inline-block;
  height: 16px;
  width: 16px;
  margin: 4px;
  text-align: center; }
  .githubContributionsGraph .day .dot {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: background 0.25s;
    -moz-transition: background 0.25s;
    -ms-transition: background 0.25s;
    -o-transition: background 0.25s;
    transition: background 0.25s;
    cursor: pointer;
    border-radius: 100%;
    margin: 0 auto;
    display: inline-block;
    background: black;
    vertical-align: middle; }
    .githubContributionsGraph .day .dot.hollow {
      border: 1px solid #BCBCBC;
      background: transparent; }
    .githubContributionsGraph .day .dot:hover {
      background: #E63F57;
      border: 1px solid #E63F57; }



